<template>
  <BaseLayout>
    <template #content>
      <ContentLayout>
        <BillingAlert
          v-if="billingMetadata && organizationId"
          :billing-metadata="billingMetadata"
          :organization-id="organizationId"
        />
        <RouterView />
      </ContentLayout>
    </template>
    <template #navbar>
      <Navbar class="nav" :breadcrumb="breadcrumb" show-ai-sparkles />
    </template>
    <template #sidebar>
      <Sidebar class="sidebar" :sections="sections" />
    </template>
    <template #chat>
      <ConsoleOmniChat />
    </template>
  </BaseLayout>
</template>

<script setup lang="ts">
import Navbar from '@/apps/console/components/workspace/Navbar.vue';
import BaseLayout from '@/components/BaseLayout.vue';
import { asyncComputed } from '@/utils/asyncComputed';
import { computed } from 'vue';
import { useRoute } from 'vue-router';
import { OrganizationModel, ProjectModel } from '../apis/cloud';
import BillingAlert from '../components/workspace/BillingAlert.vue';
import Sidebar, { type Section } from '../components/workspace/Sidebar.vue';

import ContentLayout from '@/components/ContentLayout.vue';
import {
  PhArrowCounterClockwise,
  PhBracketsSquare,
  PhCheckSquareOffset,
  PhDatabase,
  PhFilePy,
  PhFolders,
  PhGearSix,
  PhIdentificationBadge,
  PhKey,
  PhLaptop,
  PhPlug,
  PhRocket,
} from '@phosphor-icons/vue';

import { FolderAPI } from '../apis/cloud/folder';
import ConsoleOmniChat from '../components/ConsoleOmniChat.vue';

const route = useRoute();
const projectId = route.params.projectId as string;

const { result } = asyncComputed(() =>
  ProjectModel.get(projectId).then(async (project) => {
    const organization = await OrganizationModel.get(project.organizationId);
    const folderApi = new FolderAPI();
    const folders = await folderApi.list(organization.id);
    const folder = folders.find((folder) => folder.id === project.folderId);
    if (!folder) {
      throw new Error('Folder not found');
    }
    return { project, organization, folder };
  }),
);

const breadcrumb = computed(() =>
  result.value?.organization && result.value.project
    ? [
        {
          label: 'My organizations',
          path: '/organizations',
        },
        {
          label: result.value?.organization?.name,
          path: `/organizations/${result.value?.organization.id}`,
        },
        {
          label: result.value.folder.name,
          path: `/organizations/${result.value?.organization.id}#${result.value.folder.id}`,
        },
        {
          label: result.value.project.name,
          path: `/projects/${result.value.project.id}`,
        },
      ]
    : undefined,
);

const billingMetadata = computed(() => result.value?.organization.billingMetadata);
const organizationId = computed(() => result.value?.organization.id);

const sections = computed(() =>
  result.value?.project
    ? ([
        {
          name: 'Monitoring',
          items: [
            {
              name: 'Stages',
              path: 'live',
              icon: PhFilePy,
            },
            {
              name: 'Version History',
              path: 'builds',
              icon: PhRocket,
            },
            {
              name: 'Logs',
              icon: PhArrowCounterClockwise,
              path: 'logs',
            },
            {
              name: 'Tasks',
              path: 'tasks',
              icon: PhCheckSquareOffset,
            },
          ],
        },
        {
          name: 'Resources',
          items: [
            {
              name: 'Tables',
              path: 'tables',
              icon: PhDatabase,
            },
            {
              name: 'Files',
              path: 'files',
              icon: PhFolders,
            },
            {
              name: 'Connectors',
              path: 'connectors',
              icon: PhPlug,
            },
            {
              name: 'Web Editor',
              path: 'web-editor',
              icon: PhLaptop,
              unavailable: !result.value.organization.featureFlags.WEB_EDITOR,
            },
          ],
        },
        {
          name: 'Configuration',
          items: [
            {
              name: 'API Keys',
              path: 'api-keys',
              icon: PhKey,
            },
            {
              name: 'Env Vars',
              path: 'env-vars',
              icon: PhBracketsSquare,
            },
            {
              name: 'Subdomain',
              icon: PhGearSix,
              path: 'settings',
            },
            {
              name: 'Access Control',
              icon: PhIdentificationBadge,
              path: 'access-control',
            },
          ],
        },
      ] satisfies Section[])
    : [],
);
</script>

<style scoped lang="scss">
.button {
  height: 50px;
  display: flex;
  justify-content: center;
}
</style>
